.btn {
    text-decoration: none;
    display: inline-block;
    padding: 10px 15px;
    color: orangered;
    border: 1px solid red;
    border-radius: 10px;
    background-color: whitesmoke;
}

.item {
    display: block;
    /* width: 
    450px; */
    margin-bottom: 5px;
}


.list {
    display: flex;
    flex-wrap: wrap;
    gap: 80px;
    margin: 20px auto 30px;
}

form{
    width: 300px;
    height: 50px;
    padding: 8px;
    border: 1px solid #2a2a2a;
    border-radius: 5px;
    display: flex;
    gap: 12px;
    background-color: #F33A6A;
}